<template>
  <header class="mdui-appbar mdui-appbar-fixed">
    <div class="mdui-toolbar">
      <a class="mdui-btn mdui-btn-icon" @click="switchDrawer">
        <i class="mdui-icon material-icons mdui-text-color-theme-icon">menu</i>
      </a>
      <a href="" class="mdui-typo-title mdui-text-color-theme-text">NYS's Pages</a>
      <div class="mdui-toolbar-spacer"></div>
      <a class="mdui-btn mdui-btn-icon" @click="switchDarkMode">
        <i class="mdui-icon material-icons mdui-text-color-theme-icon">{{ themeIcon }}</i>
      </a>
    </div>
  </header>
</template>

<script>
import {getCurrentInstance, onMounted} from "vue"

export default {
  name: '',
  setup() {
    let themeIcon = 'brightness_4'
    const navbarId = "#Drawer"
    const { ctx } = getCurrentInstance()

    onMounted(() => {

    })

    const switchDrawer = () => {
      let inst = new ctx.$mdui.Drawer(navbarId)
      inst.toggle()
    }

    const switchDarkMode = () => {
      let body = ctx.$mdui.$('body')
      if (body.hasClass('mdui-theme-layout-dark')) {
        body.removeClass('mdui-theme-layout-dark')
        body.addClass('mdui-theme-layout-auto')
        themeIcon = 'brightness_4'
      } else {
        body.removeClass('mdui-theme-layout-auto')
        body.addClass('mdui-theme-layout-dark')
        themeIcon = 'brightness_5'
      }
    }

    return {
      themeIcon,
      navbarId,
      switchDrawer,
      switchDarkMode
    }
  }
}
</script>

<style lang="scss" scoped>

@keyframes fadein-1 {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
    transform: translateY(-24px);
  }

  100% {
    opacity: 1;
  }
}

.mdui-appbar {
  animation: fadein-1 1s ease;
}

</style>
